<template>
  <div class="hoverElement">
    <h1 class="hoverMe">Hover me</h1>
    <img class="hov" src="https://source.unsplash.com/random/1000x600.jpg" alt="">
  </div>
</template>

<script>
import orgnaicCursor from '@/assets/js/organicCursor'

export default {
  name: "App",
  mounted() {
    new orgnaicCursor({hoverElements: document.querySelectorAll('.hov')})
  }
};
</script>

<style>
html,
body {
  margin: 0px;
  padding: 0px;

  overflow: hidden;

  background: #202020;
}
canvas {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.hoverElement {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;
}
.hoverElement img {
  cursor: none;
}

.hoverMe {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  margin: 0;
  pointer-events: none;
  white-space: nowrap;

  font-size: 160px;
  color: white;
  opacity: 0.8;
  mix-blend-mode: overlay;
}

.organicCursor__text {
  position: fixed;
  z-index: 9999;
  pointer-events: none;
  top: 0;
  left: 0;
  margin: 0;

  transform: translate3d(-50%, -50%,0);
  opacity: 0;
  transition: opacity 350ms ease-out;

  font-size: 1.5em;
  font-weight: 700;
  font-family: Arial, Helvetica, sans-serif;
  color: white;
}

.organicCursor__text.show {
  transition-delay: 500ms;
  opacity: 1;
}
</style>
