
export const vertex = `
attribute vec3 position;

uniform mat4 modelViewMatrix;
uniform mat4 projectionMatrix;

void main() {
  gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
}
`;

export const fragment = `
#ifdef GL_ES
precision mediump float;
#endif

uniform vec2 u_resolution;
uniform vec2 u_mouse;
uniform float u_animation;
uniform float u_time;

const vec3 u_color = vec3(254.0/255.0, 84.0/255.0, 0.);
const float LARGEUR_CIRCLES = 12.0;
const float RAYON = 0.35;
const float BLUR = 15.0;
const float LARGEUR_CURSOR = 82.0;

float plot(float st, float pct){
return  smoothstep( pct-0.02, pct, st) -
        smoothstep( pct, pct+0.02, st);
}

float circle(in vec2 _st, in float _radius, in float blurriness){
  vec2 dist = _st-vec2(0.5);
  return 1.-smoothstep(_radius-(_radius * blurriness), _radius+(_radius * blurriness), dot(dist,dist) * 4.0);
}

vec3 mod289(vec3 x) { return x - floor(x * (1.0 / 289.0)) * 289.0; }
vec2 mod289(vec2 x) { return x - floor(x * (1.0 / 289.0)) * 289.0; }
vec3 permute(vec3 x) { return mod289(((x*34.0)+1.0)*x); }

float snoise(vec2 v) {
    const vec4 C = vec4(0.211324865405187,  // (3.0-sqrt(3.0))/6.0
                        0.366025403784439,  // 0.5*(sqrt(3.0)-1.0)
                        -0.577350269189626,  // -1.0 + 2.0 * C.x
                        0.024390243902439); // 1.0 / 41.0
    vec2 i  = floor(v + dot(v, C.yy) );
    vec2 x0 = v -   i + dot(i, C.xx);
    vec2 i1;
    i1 = (x0.x > x0.y) ? vec2(1.0, 0.0) : vec2(0.0, 1.0);
    vec4 x12 = x0.xyxy + C.xxzz;
    x12.xy -= i1;
    i = mod289(i); // Avoid truncation effects in permutation
    vec3 p = permute( permute( i.y + vec3(0.0, i1.y, 1.0 ))
        + i.x + vec3(0.0, i1.x, 1.0 ));

    vec3 m = max(0.5 - vec3(dot(x0,x0), dot(x12.xy,x12.xy), dot(x12.zw,x12.zw)), 0.0);
    m = m*m ;
    m = m*m ;
    vec3 x = 2.0 * fract(p * C.www) - 1.0;
    vec3 h = abs(x) - 0.5;
    vec3 ox = floor(x + 0.5);
    vec3 a0 = x - ox;
    m *= 1.865 - 1.894 * ( a0*a0 + h*h );
    vec3 g;
    g.x  = a0.x  * x0.x  + h.x  * x0.y;
    g.yz = a0.yz * x12.xz + h.yz * x12.yw;
    return 130.0 * dot(m, g);
}

float getNoise() {
  vec2 st = gl_FragCoord.xy/u_resolution.xy;
  st.x *= u_resolution.x/u_resolution.y;
  float color = 0.0;
  vec2 pos = vec2(st*3.) / 0.8;

  float DF = 0.0;

  // Add a random position
  float a = 0.0;
  vec2 vel = vec2(u_time*.06);
  DF += snoise(pos+vel)*.25+.25;

  // Add a random position
  a = snoise(pos*vec2(cos(u_time*0.1),sin(u_time*0.1))*0.1)*3.1415;
  vel = vec2(cos(a),sin(a));
  DF += snoise(pos+vel)*.25+.25;

  return color = smoothstep(0.1,.85, DF);
}

void main() {
vec2 st = gl_FragCoord.xy;
  st *= 1.0 / LARGEUR_CIRCLES;
  st = fract(st); // Wrap around 1.0

  // Calcul rayon cercle
  float dist = distance(gl_FragCoord.xy, u_mouse);
  float mult = circle(vec2(dist / 25.0), 10.5, 5.0);
  float appearAnimation = clamp(1. - (dist - u_animation * dist), 0., 1.) * step(0.01, u_animation);
  float handleAnimation = sin(u_time) / 250.0;
  
  float r1 = mult * RAYON / 1.1 +
    clamp( -0.8 + getNoise(), 0.0, (LARGEUR_CURSOR - dist ) / LARGEUR_CURSOR / 15.0) + handleAnimation;
  float rayon = clamp(r1, 0.0, 3.0) * appearAnimation;

  vec3 color = vec3(0.0);
  color = vec3(st,0.0);
  color = vec3(circle(st,rayon, BLUR));

  // calculer cercle de LEFT
  vec2 stL = vec2(st.x + 1., st.y);
  color += vec3(circle(stL,rayon, BLUR));
  // calculer cercle de RIGHT
  vec2 stR = vec2(st.x - 1., st.y);
  color += vec3(circle(stR,rayon, BLUR));
  // calculer cercle de UP
  vec2 stU = vec2(st.x, st.y + 1.0);
  color += vec3(circle(stU,rayon, BLUR));
  // calculer cercle de DOWN
  vec2 stD = vec2(st.x, st.y - 1.0);
  color += vec3(circle(stD,rayon, BLUR));

  // L-U
  color += vec3(circle(vec2(stL.x, stU.y),rayon, BLUR));
  // R-U
  color += vec3(circle(vec2(stR.x, stU.y),rayon, BLUR));
  // L-D
  color += vec3(circle(vec2(stL.x, stD.y),rayon, BLUR));
  // R-D
  color += vec3(circle(vec2(stR.x, stD.y),rayon, BLUR));

  vec3 contrastedColor = u_color * smoothstep(vec3(0.15), vec3(0.25), color);

gl_FragColor = vec4(contrastedColor, contrastedColor.x);
}
`;