import { Renderer, Camera, Transform, Plane, Program, Mesh } from "ogl";

import { easeOutCubic } from './Math'
import { fragment, vertex } from '../glsl/glsl'


export default class organicCursor{
  constructor({hoverElements}) {
    this.hoverElements = hoverElements
    this.cursor = {
      target: { x: 0, y: window.innerHeight },
      current: { x: 0, y: window.innerHeight },
      speed: 0.1,
      hover: false
    }
    this.animationTimer = 0
    this.timer = 0

    this.init();
  }

  init() {
    this.renderer = new Renderer({
      alpha: true,
    });
    this.gl = this.renderer.gl;

    document.body.append(this.gl.canvas);

    this.camera = new Camera(this.gl);
    this.camera.position.z = 5;

    this.initScene();
    this.initText();

    this.resize();
    this.update();

    this.setEventListeners();
  }

  initScene() {
    this.scene = new Transform();

    this.planeGeometry = new Plane(this.gl);

    this.program = new Program(this.gl, {
      vertex,
      fragment,
      uniforms: {
        u_mouse: { value: [this.cursor.current.x, this.cursor.current.y] },
        u_resolution: { value: [0, 0] },
        u_animation: {value: this.animationTimer},
        u_time: {value: this.timer}
      },
      transparent: true
    });

    this.mesh = new Mesh(this.gl, {
      geometry: this.planeGeometry,
      program: this.program,
    });
    this.mesh.setParent(this.scene);
  }

  initText() {
    this.text = document.createElement('p')
    this.text.classList.add('organicCursor__text')
    this.text.innerHTML = 'MORE'
    document.body.append(this.text)
  }

  update() {
    requestAnimationFrame(this.update.bind(this));

    if (this.animationTimer < 1 && this.cursor.hover) {
      this.animationTimer += 0.03
      this.mesh.program.uniforms.u_animation.value = easeOutCubic(this.animationTimer)
      this.timer = 0
      this.mesh.program.uniforms.u_time.value = this.timer
    } else if (this.animationTimer >= 0 && !this.cursor.hover) {
      this.animationTimer -= 0.035
      if (this.animationTimer <= 0 ) this.animationTimer = 0
      this.mesh.program.uniforms.u_animation.value = easeOutCubic(this.animationTimer)
      this.timer = 0
      this.mesh.program.uniforms.u_time.value = this.timer
    } else {
      this.timer += 0.025
      this.mesh.program.uniforms.u_time.value = this.timer
    }

    //text
    const x = this.cursor.target.x;
    const y = (this.cursor.target.y - window.innerHeight) * -1;
    let scale = this.animationTimer < 0.5 ? this.animationTimer * 2 : 1;
    this.text.style.transform = `translate3d(calc(${x}px - 50%), calc(${y}px - 50%), 0) scale(${scale})`
    if(this.cursor.hover) {
      this.text.classList.add('show')
    } else {
      this.text.classList.remove('show')
    }

    this.renderer.render({ scene: this.scene, camera: this.camera });
  }

  resize() {
    this.screen = {
      width: window.innerWidth,
      height: window.innerHeight,
    };
    this.mesh.program.uniforms.u_resolution.value = [this.screen.width, this.screen.height]

    this.renderer.setSize(this.screen.width, this.screen.height);

    this.camera.perspective({
      aspect: this.gl.canvas.width / this.gl.canvas.height,
    });

    // size plane
    const fov = this.camera.fov * (Math.PI / 180);
    const height = 2 * Math.tan(fov / 2) * this.camera.position.z;
    const width = height * this.camera.aspect;

    this.mesh.scale.x = width;
    this.mesh.scale.y = height;
  }

  mooveCursor(e) {
    this.cursor.target.x = e.clientX;
    this.cursor.target.y = -e.clientY + window.innerHeight;

    this.mesh.program.uniforms.u_mouse.value = [this.cursor.target.x, this.cursor.target.y]
  }

  setEventListeners() {
    window.addEventListener("resize", this.resize.bind(this));
    window.addEventListener("mousemove", this.mooveCursor.bind(this));

    this.hoverElements.forEach((el) => {
      console.log(el)
      el.addEventListener('mouseover', () => {
        this.cursor.hover = true
      })
      el.addEventListener('mouseout', () => {
        this.cursor.hover = false
      })
    })
  }
}